import React from "react";
import {findAllByDisplayValue} from "@testing-library/react";
import bg from "../off_bg.jpg"
import "./offset.css";

function RotorOffset ({offset}) {
    return(
        <div className="container mt-5">
            <div className="row justify-content-center">
                <div className="col-1 digit" >
                    <p className="mt-2">{offset.III_offset}</p>
                </div>
                <div className="col-1 digit">
                    <p className="mt-2">{offset.II_offset}</p>
                </div>
                <div className="col-1 digit">
                    <p className="mt-2">{offset.I_offset}</p>
                </div>
            </div>
        </div>
    );
};

export default RotorOffset;