import React, {Component} from "react";
import Navigation from "./NavComponent";
import Keyboard from "./KeyboardComponent";
import RotorOffset from "./RotorOffsetComponent";
import {KEYS} from "../shared/keys";
import {ROTOR} from "../shared/rotors";
import {KEYCODE} from "../shared/keycode";
import {CODEKEY} from "../shared/codekey";


class Main extends Component {

    constructor(props) {
        super(props);

        this.state = {
            keys: KEYS,
            rotors: ROTOR,
            keycode: KEYCODE,
            offset: {
                I_offset: Math.floor(Math.random()*26),
                II_offset: Math.floor(Math.random()*26),
                III_offset: Math.floor(Math.random()*26)
            },
            codekey: CODEKEY
        }
        this.cipher.bind(this);
        this.rotorTurn.bind(this);
        this.wrap.bind(this)
        this.updateOffset = this.updateOffset.bind(this);
    }

    componentDidMount() {
        document.addEventListener("keydown", this.onKeyDown.bind(this));
    }

    wrap(num){
        if(num > 25)
            num = num-26;
        if(num < 0)
            num = 26+num;
        return num;
    }

    cipher(keycode) {
        let enKey = keycode;
        console.log("enkey : " + enKey);
        enKey = this.state.rotors.static[enKey];
        enKey = this.wrap(this.state.rotors.I[this.wrap(enKey + this.state.offset.I_offset)]- this.state.offset.I_offset);
        enKey = this.wrap(this.state.rotors.II[this.wrap(enKey+ this.state.offset.II_offset)]- this.state.offset.II_offset);
        enKey = this.wrap(this.state.rotors.III[this.wrap(enKey+ this.state.offset.III_offset)]- this.state.offset.III_offset);
        enKey = this.state.rotors.reflector[enKey];
        enKey = this.wrap(this.state.rotors.III.indexOf(this.wrap(enKey+ this.state.offset.III_offset))- this.state.offset.III_offset);
        enKey = this.wrap(this.state.rotors.II.indexOf(this.wrap(enKey+ this.state.offset.II_offset))- this.state.offset.II_offset);
        enKey = this.wrap(this.state.rotors.I.indexOf(this.wrap(enKey+ this.state.offset.I_offset))- this.state.offset.I_offset);
        enKey = this.state.rotors.static.indexOf(enKey);
        return(enKey);
    }

    rotorTurn(){
        let I = this.state.offset.I_offset;
        let II = this.state.offset.II_offset;
        let III = this.state.offset.III_offset;

        I++;
        if(I>25){
            I-=26;
            II++;
        }
        if(II>25){
            II-=26;
            III++;
        }
        if(III>25){
            III-=26;
        }

        this.setState({
            offset: {
                I_offset: I,
                II_offset: II,
                III_offset: III
            }
        });

        console.log("rotor position: " +this.state.offset.III_offset+this.state.offset.II_offset+this.state.offset.I_offset);
    }

    onKeyDown(event) {
        const char = event.key.toUpperCase();
        console.log(char);
        if(this.state.keycode[char] === undefined) return;
        const encryptedChar = this.state.codekey[this.cipher(this.state.keycode[char])];
        const element = document.getElementById(encryptedChar);
        const tube = this.state.keys.filter((key) => key.id === encryptedChar)[0];
        if(tube) {
            console.log("encryptedChar: "+tube.on_src);
            this.rotorTurn();
            element.src = tube.on_src;
            setTimeout(() => { element.src = tube.off_src; },1000);
        }
    }

    updateOffset(input, rotor){
        this.setState(prevState => ({
            offset : {
                ...prevState.offset,
                [rotor+"_offset"]: this.wrap(input)
            }
        }));
        console.log(this.state.offset);
    }

    render() {
        return(
            <div className="Main mb-5 pb-5">
                <Navigation update={this.updateOffset}/>
                <RotorOffset offset={this.state.offset}/>
                <Keyboard keys={this.state.keys} onKeyDown={this.onKeyDown}/>
            </div>
        );
    }
}

export default Main;