import React from "react";
import {Form, Label, Navbar, NavbarBrand, NavItem} from "reactstrap";


function Navigation({update}) {
    function updateI(e){
        let input=Number.parseInt(e.target.value);
        if(!input)
            input = 0;
        update(input,'I');
    }
    function updateII(e){
        let input=Number.parseInt(e.target.value);
        if(!input)
            input = 0;
        update(input,'II');
    }
    function updateIII(e){
        let input=Number.parseInt(e.target.value);
        if(!input)
            input = 0;
        update(input,'III');
    }

    return(
        <div>
            <Navbar dark>
                <div className="container-fluid" style={{color: "white"}}>
                    <NavbarBrand className="ml-2" href="#">
                        <h2 style={{fontFamily: "monospace" }}><strong>ENIGMA</strong></h2>
                    </NavbarBrand>
                    <div>
                        <NavItem className="pr-5">
                            <Form>
                                <Label  for="IIIoff">
                                    Enter Settings:
                                </Label>
                                <input type="number" className="ml-1" id="IIIoff" style={{width: 30, border: "2px ridge white", color: "white"}} onInput={updateIII} />
                                <input type="number" className="ml-1" id="IIoff" style={{width: 30, border: "2px ridge white", color: "white"}} onInput={updateII} />
                                <input type="number" className="ml-1" id="Ioff" style={{width: 30, border: "2px ridge white", color: "white"}} onInput={updateI} />
                            </Form>
                        </NavItem>
                    </div>
                </div>
            </Navbar>
        </div>
    );
}

export default Navigation;