import React, {Component} from "react";

function Keyboard({keys, onKeyDown}) {
        function keyboardRow(row){
            const KEYS = keys.map((char) => {
                if(char.row === row)
                    return(
                        <div key={char.id} className="col-1 ml-3">
                            <img id={char.id} src={char.off_src} alt="B" width="100" />
                        </div>
                    );
                else return (<></>);
            });
            return KEYS;
        }

        return(
            <div className="container-fluid" onKeyDown={onKeyDown}>
                <div className="row mt-5 justify-content-center">
                    {keyboardRow("top")}
                </div>
                <div className="row mt-3 justify-content-center">
                    {keyboardRow("mid")}
                </div>
                <div className="row mt-3 justify-content-center">
                    {keyboardRow("bottom")}
                </div>
                <div style={{height: 160}}>

                </div>
            </div>
        );
}

export default Keyboard;